// @flow
import { useContext } from "react"
import { useLimioContext } from "../context"

export function usePage() {
  const { shop } = useContext(useLimioContext())

  const getCampaign = () => {
    const { campaign } = shop
    return campaign
  }

  const getOffers = () => {
    const { offers } = shop
    return offers
  }

  const getTag = () => {
    const { tag } = shop
    return tag
  }

  return { getCampaign, getOffers, getTag }
}
