// @flow
import { useContext } from "react"
import { useLimioContext } from "../context"

export function useNavigation() {
  const { shop } = useContext(useLimioContext())

  const goToCheckout = () => {
    const { goToCheckout: contextGoToCheckout } = shop

    return contextGoToCheckout()
  }

  return { goToCheckout }
}
