// @flow
import { useContext, useCallback } from "react"
import { useLimioContext } from "../context"

export function useBasket() {
  const { shop } = useContext(useLimioContext())
  const { basketItems, addToBasket } = shop

  const externalAddToBasket = useCallback(
    (offer, options = {}) => {
      const { quantity = 1, selectedProducts, redirectUrl = "" } = options
      addToBasket(offer, selectedProducts, quantity, redirectUrl)
    },
    [addToBasket]
  )

  return { addToBasket: externalAddToBasket, basketItems }
}
